import { Component, forwardRef, Input, OnInit } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  // eslint-disable-next-line @angular-eslint/component-selector
  selector: 'color-picker-control',
  templateUrl: './color-picker-control.component.html',
  styleUrls: ['./color-picker-control.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => ColorPickerControlComponent),
      multi: true,
    },
  ],
})
export class ColorPickerControlComponent implements OnInit, ControlValueAccessor {
  @Input() colors: string[];
  defaultColor: string;
  value!: string;
  isDisabled: boolean;
  onChange: any = () => {
    // eslint-disable-next-line no-undef
    console.log();
  };
  onTouched: any = () => {
    // eslint-disable-next-line no-undef
    console.log();
  };

  constructor() {}
  ngOnInit(): void {
    this.defaultColor = this.colors[0];
  }

  writeValue(value: string): void {
    if (value) {
      const exist = this.colors.includes(value);
      if (exist) {
        this.value = value;
        this.defaultColor = value;
      } else {
        // eslint-disable-next-line no-undef
        setTimeout(() => {
          this.value = this.defaultColor;
          this.onChange(this.value);
        }, 10);
      }
    } else {
      // eslint-disable-next-line no-undef
      setTimeout(() => {
        this.value = this.defaultColor;
        this.onChange(this.value);
      }, 10);
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  setSelectedColorValue(color: string) {
    this.defaultColor = color;
    this.value = color;
    this.onChange(this.value);
  }
}
