/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable no-unused-vars */
/* eslint-disable @typescript-eslint/no-non-null-assertion */
/* eslint-disable @typescript-eslint/consistent-type-imports */
/* eslint-disable no-undef */
import type { AfterViewInit, ElementRef, OnInit } from '@angular/core';
import { Component, ViewChild, ViewEncapsulation } from '@angular/core';
import * as htmlToImage from 'html-to-image';
import type { OverlayPanel } from 'primeng/overlaypanel';
import { ModalService } from 'src/app/shared/components/confrimation-modal/service/modal.service';
import { SessionService } from 'src/app/shared/services/session-service/session.service';
import { AlertService } from './../../../shared/services/alert-service/alert-service.service';
import { AdvancedDriverService } from './../../advanced-driver/services/advanced-driver.service';
import type { IMetricBody, IMetrics, IScorecards } from './../interfaces/score-metric.interface';
import { ScoreMetricService } from './../services/score-metric.service';
var cloneDeep = require('lodash.clonedeep');
var jsPDF = require('jspdf');

@Component({
  selector: 'app-score-metrics',
  templateUrl: './score-metrics.component.html',
  styleUrls: ['./score-metrics.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ScoreMetricsComponent implements OnInit, AfterViewInit {
  @ViewChild('createScoreCard') createScoreCard!: OverlayPanel;
  @ViewChild('editScoreName') editScoreName!: OverlayPanel;
  @ViewChild('addScoreMetric') addScoreMetric!: OverlayPanel;
  @ViewChild('editScoreMetricElement') editScoreMetricElement!: OverlayPanel;
  @ViewChild('editScoreMetricLimit') editScoreMetricLimit!: OverlayPanel;
  @ViewChild('emptyScoreCard') emptyScoreCard: any;
  @ViewChild('content') content!: ElementRef;

  loadingMode = {};
  loader = {
    type: 'pulsing',
    themeColor: 'primary',
    size: 'large',
  };
  collapsed = {};
  scoreCardList: IScorecards[] = [];
  selectedCard!: IScorecards;
  virtualSelectedCard!: IScorecards;
  selectedScoreMetricCard!: IMetrics;
  ngScoreCardName = '';
  alertRules: any = [];
  ngScoreCardDescription = '';
  editMode = {};
  virtualscoreCardList: IScorecards[] = [];
  selectedScoreCard!: IScorecards | any;
  scorecardMode: 'add' | 'edit' = 'add';
  currentTenant: { id: number; name: string };
  colors: string[];
  constructor(
    private service: ScoreMetricService,
    private alertService: AlertService,
    private driverScoreService: AdvancedDriverService,
    private modalService: ModalService,
    private session: SessionService
  ) {}

  ngOnInit(): void {
    this.currentTenant = this.session.currentTenant;
    this.colors = this.session.colors;
    this.service.getAlertRules().subscribe((res) => (this.alertRules = res.AlertRules));
  }

  collapse(scoreId: number) {
    if (this.collapsed[scoreId] && this.collapsed[scoreId].value) {
      document.getElementById('collapseExample' + scoreId)?.classList.add('collapsed');
      this.collapsed[scoreId] = {
        value: false,
      };
    } else {
      document.getElementById('collapseExample' + scoreId)?.classList.remove('collapsed');
      this.collapsed[scoreId] = {
        value: true,
      };
    }
  }

  ngAfterViewInit() {
    this.getScoreCards();
  }

  getScoreCards() {
    this.driverScoreService.getScorecards(this.currentTenant.id).subscribe((res) => {
      res.forEach((e) => {
        this.collapsed[e.Id] = {
          value: false,
        };
      });
      this.virtualscoreCardList = res;
      this.scoreCardList = res;
    });
  }

  submitScoreCard(form: any) {
    if (form.valid) {
      this.driverScoreService.createScorecard(form.value).subscribe((res: any) => {
        this.createScoreCard.hide();
        if (res.Result == 'CREATED') {
          this.alertService.showAlert('success', '[[[[Done]]]]', '[[[[Submitted successfully]]]]!');
          this.getScoreCards();
          this.clearScoreCard();
        }
      });
    }
  }

  openModal(id: string, scoreCard: IScorecards) {
    this.selectedScoreCard = scoreCard;
    this.modalService.open(id);
  }

  deleteScoreCard(id: string) {
    this.modalService.close(id);
    this.driverScoreService.deleteScorecard(this.selectedScoreCard.Id).subscribe((res: any) => {
      if (res.Result == 'DELETED') {
        this.virtualscoreCardList = this.virtualscoreCardList.filter((e) => e.Id != this.selectedScoreCard.Id);
        this.scoreCardList = this.scoreCardList.filter((e) => e.Id != this.selectedScoreCard.Id);
        this.alertService.showAlert('success', '[[[[Deleted Successfully]]]]!', '[[[[Deleted]]]]!');
      }
    });
  }

  addScoreCardMetric(event: any, scoreCard: IScorecards) {
    this.scorecardMode = 'add';
    this.selectedCard = scoreCard;
    this.addScoreMetric.show(event);
  }

  editScoreMetric(value: { event: any; score: IScorecards; metric: IMetrics }) {
    // this.editScoreMetricElement.hide();

    setTimeout(() => {
      this.selectedCard = value.score;
      this.selectedScoreMetricCard = value.metric;
      this.editScoreMetricElement.show(value.event);
    }, 250);
  }

  editCard(event: any, target: string, scoreCard: IScorecards, scoreMetric?: IMetrics) {
    if (target == 'name') {
      this.selectedCard = scoreCard;
      this.virtualscoreCardList = cloneDeep(this.scoreCardList);
      this.virtualSelectedCard = cloneDeep(scoreCard);

      this.editScoreName.toggle(event);
    } else if (target == 'AlertRule') {
      this.selectedCard = scoreCard;

      if (scoreMetric) this.selectedScoreMetricCard = scoreMetric;

      this.editScoreMetricLimit.toggle(event);
    }
  }

  deleteScoreMetric(value: { metric: IMetrics; score: IScorecards }) {
    this.virtualscoreCardList = cloneDeep(this.scoreCardList);
    const scoreCard = this.scoreCardList.find((e) => e.Id == value.score.Id);
    const virtualScoreCard = this.virtualscoreCardList.find((e) => e.Id == value.score.Id)!;
    virtualScoreCard.Metrics = cloneDeep(scoreCard?.Metrics);

    const allnewMetrics = scoreCard?.Metrics.every((e) => e.index);
    if (scoreCard) {
      if (allnewMetrics) {
        if (value.metric.index) {
          scoreCard.Metrics = scoreCard?.Metrics.filter((e) => e.index != value.metric.index);
        }
        if (scoreCard.Metrics.length == 0) {
          this.editMode[value.score.Id] = {
            editing: false,
          };
        }
      } else {
        const metricesExists = scoreCard?.Metrics.some((e) => e.Id == value.metric.Id);
        if (value.metric.index) {
          scoreCard.Metrics = scoreCard?.Metrics.filter((e) => e.index != value.metric.index);
        } else {
          scoreCard.Metrics = scoreCard?.Metrics.filter((e) => e.Id != value.metric.Id);
          if (metricesExists) {
            this.editMode[value.score.Id] = {
              editing: true,
              scoreCard,
            };
          }
        }
      }
    }
  }

  generatePdf() {
    let doc = new jsPDF('l', 'px', 'a4');
    this.alertService.showAlert('info', '[[[[Generating Pdf]]]]...');

    this.convertHtmlToImage(document.getElementById('MyDIv'))
      .then((element: any) => {
        const imageElement = this.createImageElement(element, 'imgPdf');
        const imgProps = doc.getImageProperties(element);
        const pdfWidth = doc.internal.pageSize.getWidth();
        const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width - 10;

        doc.setFillColor('#25282D');
        //
        doc.addImage(imageElement, 'PNG', 0, 0, pdfWidth, pdfHeight);
        doc.save('[[[[Scorecard]]]]');
      })
      .finally(() => {
        setTimeout(() => {
          const alertNames = document.getElementById('MyDIv')!.getElementsByTagName('h4');
          const scoreCardNames = document.getElementById('MyDIv')!.getElementsByClassName('score-card-name');
          for (let i = 0; i < alertNames.length; i++) {
            alertNames.item(i)?.classList.remove('pdf-header');
          }
          for (let j = 0; j < scoreCardNames.length; j++) {
            scoreCardNames.item(j)?.classList.remove('pdf-header');
          }
          document.getElementById('imgPdf')?.remove();
        }, 1000);
      });
  }

  convertHtmlToImage(containerElement: HTMLElement | null) {
    return new Promise((res) => {
      const alertNames = document.getElementById('MyDIv')!.getElementsByTagName('h4');
      const scoreCardNames = document.getElementById('MyDIv')!.getElementsByClassName('score-card-name');
      for (let i = 0; i < alertNames.length; i++) {
        alertNames.item(i)?.classList.add('pdf-header');
      }
      for (let j = 0; j < scoreCardNames.length; j++) {
        scoreCardNames.item(j)?.classList.add('pdf-header');
      }
      htmlToImage.toPng(containerElement!, { backgroundColor: '#25282D' }).then((imageBase64) => {
        res(imageBase64);
      });

      // containerElement!.style.width = `${defaultElementWidth}px`;
    });
  }

  createImageElement = (base64: string, imageId: string) => {
    const imageElement = document.createElement('img');
    imageElement.src = base64;
    imageElement.id = imageId;
    // to hide the image from dom before print
    imageElement.classList.add('hidden-image');
    return imageElement;
  };

  onActionCompleted = (value: IMetricBody) => {
    //  this.virtualscoreCardList = this.scoreCardList;

    this.virtualscoreCardList = cloneDeep(this.scoreCardList);
    const scoreCard = this.scoreCardList.find((e) => e.Id == value.ScoreCardId);
    const virtualScoreCard = this.virtualscoreCardList.find((e) => e.Id == value.ScoreCardId)!;
    virtualScoreCard.Metrics = cloneDeep(scoreCard?.Metrics);

    //const metricesExists = scoreCard?.Metrics.some(e => e.Id == value.ScoreMetricId);
    if (value.editmode && !value.index) {
      scoreCard?.Metrics.filter((e) => e.Id == value.ScoreMetricId).map((e) => {
        (e.AlertRule = value.AlertRule),
          (e.Color = value.Color),
          (e.Limit = value.Limit),
          (e.Weight = value.Weight),
          (e.CostImplication = value.CostImplication),
          (e.Id = value.ScoreMetricId);
      });
    } else if (value.editmode && value.index) {
      scoreCard?.Metrics.filter((e) => e.index == value!.index).map((e) => {
        (e.AlertRule = value.AlertRule),
          (e.Color = value.Color),
          (e.Limit = value.Limit),
          (e.Weight = value.Weight),
          (e.CostImplication = value.CostImplication),
          (e.Id = 0),
          (e.index = value.index);
      });
    } else {
      scoreCard?.Metrics.push({
        AlertRule: value.AlertRule,
        Color: value.Color,
        Limit: value.Limit,
        Weight: value.Weight,
        CostImplication:value.CostImplication,
        Id: 0,
        index: value.index,
      });
    }
    this.editMode[value.ScoreCardId] = {
      editing: true,
      scoreCard,
    };
    this.onCancel();
  };

  updateScoreCard() {
    this.loadingMode[this.selectedCard.Id] = {
      loading: true,
    };
    this.driverScoreService
      .updateScorecard({
        id: this.selectedCard.Id,
        name: this.selectedCard.Name,
        description: this.selectedCard.Description,
      })
      .subscribe((res: any) => {
        if (res.Result == 'UPDATED') {
          this.driverScoreService.getScorecardById(this.selectedCard.Id, this.currentTenant.id).subscribe((res) => {
            if (res) {
              this.selectedCard = res;
              this.scoreCardList.filter((e) => e.Id == this.selectedCard.Id).map((c) => (c = res));
              this.virtualSelectedCard = cloneDeep(this.selectedCard);
              this.virtualscoreCardList = cloneDeep(this.scoreCardList);
              this.loadingMode[this.selectedCard.Id].loading = false;
              this.alertService.showAlert('success', '[[[[Done]]]]', '[[[[Updated Successfully]]]]!');
              this.editScoreName.hide();
            }
          });
        } else {
          this.loadingMode[this.selectedCard.Id].loading = false;
        }
      });
  }

  saveChanges(scoreId: number) {
    this.loadingMode[scoreId] = {
      loading: true,
    };
    this.service.BulkUpdate(this.editMode[scoreId].scoreCard).subscribe((res: any) => {
      if (res.Result == 'UPDATED') {
        this.editMode[scoreId].editing = false;
        this.alertService.showAlert('success', '[[[[Updated Successfully]]]]', '[[[[Succcess]]]]!');
        this.driverScoreService.getScorecardById(scoreId, this.currentTenant.id).subscribe((res) => {
          if (res) {
            this.scoreCardList.filter((e) => e.Id == scoreId).forEach((c) => (c.Metrics = res.Metrics));
            this.editMode[scoreId].editing = false;
            this.loadingMode[scoreId].loading = false;
          }
        });
      } else {
        this.loadingMode[scoreId].loading = false;
      }
    });
  }

  cancelUpdate() {
    this.selectedCard = this.virtualSelectedCard;
    this.scoreCardList = this.virtualscoreCardList;
    this.editScoreName.hide();
  }

  cancelChanges(scorecardId: number) {
    this.loadingMode[scorecardId] = {
      loading: true,
    };

    this.driverScoreService.getScorecardById(scorecardId, this.currentTenant.id).subscribe((res) => {
      if (res) {
        this.scoreCardList.filter((e) => e.Id == scorecardId).forEach((c) => (c.Metrics = res.Metrics));
        this.loadingMode[scorecardId].loading = false;
      }
    });
    this.editMode[scorecardId].editing = false;
  }

  closeModal(id: string) {
    this.selectedScoreCard = {};
    this.modalService.close(id);
  }

  clearScoreCard() {
    this.ngScoreCardName = '';
    this.ngScoreCardDescription = '';
    this.createScoreCard.hide();
    this.editScoreName.hide();
  }

  onCancel() {
    this.editScoreName.hide();
    this.addScoreMetric.hide();
    this.editScoreMetricElement.hide();
    this.editScoreMetricLimit.hide();
  }
}
