import { Component, EventEmitter, Input, Output } from '@angular/core';
import { slideInOutAnimation } from 'src/app/shared/animations/animations';
import type { IMetrics, IScorecards } from './../../../interfaces/score-metric.interface';

@Component({
  selector: 'app-score-card',
  templateUrl: './score-card.component.html',
  styleUrls: ['./score-card.component.scss'],
  animations: slideInOutAnimation,
})
export class ScoreCardComponent {
  @Input() metric!: IMetrics;
  @Input() score!: IScorecards;
  @Output() edit = new EventEmitter<any>();
  @Output() delete = new EventEmitter<any>();
  constructor() {}

  editScoreMetric(event: any, score: IScorecards, metric: IMetrics) {
    this.edit.emit({ event, score, metric });
  }

  deleteScoreMetric(metric: IMetrics, score: IScorecards) {
    this.delete.emit({ metric, score });
  }
}
