/* eslint-disable no-undef */
import type { AfterViewInit, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import type { IMetricBody, IMetrics } from '../../interfaces/score-metric.interface';

@Component({
  selector: 'app-edit-view',
  templateUrl: './edit-view.component.html',
  styleUrls: ['./edit-view.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class EditViewComponent implements OnInit, AfterViewInit, OnChanges {
  @Input() pageMode: 'add' | 'edit' = 'edit';
  @Input() alertRules: any = [];
  @Input() scoreCardId = -1;
  @Input() colors: string[] = [];
  @Input() metricModel!: IMetrics;
  @Output() addCompleted = new EventEmitter<IMetricBody>();
  @Output() editClosed = new EventEmitter<boolean>();
  ngScoreMetric!: FormGroup;
  submitted = false;
  alertRulesData: any = [];

  public get elements() {
    return this.ngScoreMetric.controls;
  }

  constructor(private fb: FormBuilder) {}

  ngOnInit(): void {
    this.createForm();
  }

  ngOnChanges(_simpleChanges: SimpleChanges) {
    if (_simpleChanges && _simpleChanges.alertRules && _simpleChanges.alertRules.currentValue) {
      this.alertRulesData = _simpleChanges.alertRules.currentValue.slice();
    }
  }

  ngAfterViewInit() {
    if (this.pageMode == 'edit') {
      this.ngScoreMetric.patchValue({
        color: this.metricModel.Color,
        weight: this.metricModel.Weight,
        CostImplication: this.metricModel.CostImplication,
        limit: this.metricModel.Limit,
        alertRule: this.metricModel.AlertRule.Id,
      });
    }
    else if (this.pageMode == 'add')
    {
      this.ngScoreMetric.value.CostImplication = 0;
    }
  }

  createForm() {
    this.ngScoreMetric = this.fb.group({
      color: ['', Validators.required],
      weight: ['', [Validators.required, Validators.min(1), Validators.max(100)]],
      CostImplication: ['', [Validators.required]],
      limit: ['', [Validators.required, Validators.min(1), Validators.max(1000)]],
      alertRule: [null, Validators.required],
    });
  }

  cancelAction() {
    this.editClosed.emit(true);
  }

  colorPicker() {
    (document.getElementsByClassName('p-colorpicker-preview')[0] as any).click();
  }

  /**
   * Gets random int
   * @param min
   * @param max
   * @returns random int - min & max inclusive
   */
  getRandomInt(min: number, max: number): number {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  handleFilter(value: string) {
    this.alertRulesData = this.alertRules.filter(
      (s: { Name: string }) => s.Name.toLowerCase().indexOf(value.toLowerCase()) !== -1
    );
  }

  submitForm(form: FormGroup) {
    this.submitted = true;
    console.log("sdfsdf")
    if (form.valid) {
      const submitForm: IMetricBody = {
        AlertRule: {
          Id: form.value.alertRule,
          Name: this.alertRules.find((e: any) => e.Id == form.value.alertRule).Name,
        },
        Color: form.value.color,
        Weight: parseFloat(form.value.weight),
        CostImplication: parseFloat(form.value.CostImplication),
        Limit: form.value.limit,
        ScoreCardId: this.scoreCardId,
        ScoreMetricId: this.pageMode == 'add' ? this.getRandomInt(350, 2050) : this.metricModel.Id,
        editmode: this.pageMode == 'add' ? false : true,
      };
      if (this.pageMode == 'add') {
        submitForm.index = this.getRandomInt(350, 2050);
      } else if (this.metricModel.index) {
        submitForm.index = this.metricModel.index;
      }

      this.addCompleted.emit(submitForm);
    }
  }
}
