/* eslint-disable @typescript-eslint/consistent-type-imports */
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import type { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import type { IMetricBody } from '../interfaces/score-metric.interface';

@Injectable({
  providedIn: 'root',
})
export class ScoreMetricService {
  private readonly scorecardsEndpoint = environment.APIUrl + 'DriverBehavior/Scorecards/';

  constructor(private http: HttpClient) {}

  //! Scorecards Api's
  public deleteScoreMetric(id: number) {
    return this.http.post(`${this.scorecardsEndpoint}Delete?id=${id}`, {});
  }

  public BulkUpdate(Metrics: { id: number; Metrics: IMetricBody }[]) {
    return this.http.post(`${this.scorecardsEndpoint}BulkUpdate`, Metrics);
  }

  //! Alert Rules
  public getAlertRules(): Observable<{
    AlertRules: { Id: number; Name: string }[];
  }> {
    return this.http.get<{ AlertRules: { Id: number; Name: string }[] }>(`${this.scorecardsEndpoint}AlertRules`);
  }
}
